.circular-container {
    position: relative;
    margin: calc(100px / 2 + 0px);
    display: inline-block;
}

.circular-container:before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: calc(100% - 2px * 2);
    height: calc(100% - 2px * 2);
    border-radius: 50%;
}

.circular-container .circle {
    position: absolute;
    z-index: 2;
    top: 50%;
    left: 50%;
    margin: calc(-100px / 2);
    border-radius: 50%;
}

.circular-container .circle:hover {
    z-index: 5;
}

